import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})


export class GeralService {

  readonly url = 'http://projetocmvbackend.herokuapp.com/rota'//'http://localhost:5000/rota';

  // Summary: Construtor do serviço
  // Params: criação http para requisições
  constructor(private http: HttpClient) {}

  // Summary: Chama método do backend de forma Genérica
  // Params: url Endpoint
  // return: json
  public getEndpoint = (endpoint) => this.http.get(`${this.url}/${endpoint}`);
}
