import { GeralService } from './../../services/geral.service';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatTableDataSource } from '@angular/material';
import { FormGroup, FormBuilder } from '@angular/forms';

@Component({
  selector: 'app-pagina-configuracao',
  providers: [ GeralService ],
  templateUrl: './pagina-configuracao.component.html',
  styleUrls: ['./pagina-configuracao.component.scss']
})

export class PaginaConfiguracaoComponent implements OnInit {

  displayedColumns = ['comodo', 'tempo', 'acoes'];
  dataSource: MatTableDataSource<AlertaRestricao>;

  // Restricoes que não foram adicionadas no banco ainda
  listaRestricoesAdd: AlertaRestricao[];

  // Atributos que escondem opções
  public hiddenBanheiro: string;
  public hiddenCozinha: string;
  public hiddenSala: string;
  public hiddenQuarto: string;

  constructor(private service: GeralService) {
    this.dataSource = new MatTableDataSource();
    this.listaRestricoesAdd = [];
  }

  ngOnInit() {
    this.loadRestricoes();
  }

  public async loadRestricoes() {
    await this.service.getEndpoint('getRestricoes').subscribe((data: any) => {
      console.log('data', data);
    });
  }

  // add na tabela antes de salvar
  addAlerta(com, tem) {

    if (!tem) {
      alert ('É necessário preencher o tempo estimado!');
    } else {

      this.dataSource.data.push({comodo: com, tempo: tem});
      this.dataSource.filter = '';
      this.listaRestricoesAdd.push({comodo: com, tempo: tem});

      switch (com) {

        case 'banheiro':
          this.hiddenBanheiro = 'hidden';
          break;
        case 'cozinha':
          this.hiddenCozinha = 'hidden';
          break;
        case 'quarto':
          this.hiddenQuarto = 'hidden';
          break;
        case 'sala':
          this.hiddenSala = 'hidden';
          break;

      }
    }
  }
  salvarRestricoes() {

    if (this.listaRestricoesAdd.length === 0) {
      alert('Nenhuma Restrição nova foi adicionada!');

    } else {
      alert('aqui vou salvar no banco de Dados :D');

    }

  }

  // Remove da tela e da lista de adiçao
  deletarRestricao(data) {

    this.dataSource.data.forEach((element, index) => {
      if (element.comodo === data.comodo) {
        this.dataSource.data.splice(index, 1);
        this.listaRestricoesAdd.splice(index, 1);
      }
    });

    this.dataSource.filter = '';

    switch (data.comodo) {
      case 'banheiro':
        this.hiddenBanheiro = '';
        break;
      case 'cozinha':
        this.hiddenCozinha = '';
        break;
      case 'quarto':
        this.hiddenQuarto = '';
        break;
      case 'sala':
        this.hiddenSala = '';
        break;
    }
    console.log(this.listaRestricoesAdd);
  }

  mostraRestricoes() {

    // Traz do banco todas as restrições
  }

}

export interface AlertaRestricao {
  comodo: string;
  tempo: number;
}
